.show-SidebarCntnr {
    height: 100vh;
    width: 270px;
    /* border: 1px solid red; */
    background-color: silver;
    display: flex;
    justify-content: right;
    position: fixed;
    top: 0;
    transition: 0.4s;
    transition-timing-function: ease-in;
    z-index: 3;
}

.hide-SidebarCntnr {
    min-height: 100vh;
    width: 0;
    /* border: 1px solid red; */
    background-color: silver;
    display: flex;
    justify-content: right;
    position: fixed;
    /* left: -170px; */
    top: 0;
    transition: 0.4s;
    transition-timing-function: ease-in;
    z-index: 3;
}

.sidebar {
    height: calc(100vh - 70px);
    width: 270px;
    /* border: 1px solid blue; */
    display: flex;
    flex-direction: column;
    align-items: center;
}

.hamburgerCntnr {
    padding: 0 3px;
    cursor: pointer;
    border-radius: 2px;
    /* border: 1px solid red; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: fixed;
    left: 5px;
    top: 14px;
    z-index: 1;
}

.hamburgerCntnr:hover {
    background-color: rgb(0, 40, 40);
}

.hamburgerCntnr .hamburger-line {
    height: 1.2px;
    width: 32px;
    margin: 5px 0;
    /* border: 1px solid blue; */
    background-color: white;
}


/* css for make cross icon */
.crossCntnr {
    padding: 3.5px 1px;
    cursor: pointer;
    border-radius: 2px;
    /* border: 1px solid red; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: fixed;
    left: 270px;
    top: 16px;
    z-index: 5;
}
.crossCntnr .cross-line {
    height: 1.2px;
    width: 35px;
    margin: 5px 0;
    background-color: white;
}
.crossCntnr .line-1 {
    transform: rotate(45deg);
    transition: 0.5s;
    position: relative;
    top: 5px;
}
.crossCntnr .line-2 {
    display: none;
    transform: 0.3s;
}

.crossCntnr .line-3 {
    transform: rotate(-45deg);
    transition: 0.5s;
    position: relative;
    top: -5px
}


.sidebar-header {
    font-size: 30px;
}

.sidebar ul {
    width: 100%;
    margin-top: 15px;
    /* border:  1px solid red; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: 0.5s;
}

.sidebar .list {
    width: 99%;
    margin: 3px 0;
    /* border:  1px solid red; */
    display: flex;
}

.sidebar .list .sidebar-item {
    height: 40px;
    width: 100%;
    font-size: 19px;
    /* font-weight: 100; */
    color: black;
    text-decoration: none;
    border-radius: 2px;
}

.sidebar-item span {
    height: 100%;
    width: 100%;
    /* border:  1px solid red; */
    display: flex;
    justify-content: center;
    align-items: center;
}

.sidebar-item span:hover {
    color: white;
    background-color: rgb(0, 20, 20);
}